* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.page {
  min-height: 100vh;
  padding: 15px;
}

.page .content-area {
  width: calc(100% - 250px);
  min-height: 100%;
  transition: .3s;
  float: right;
  will-change: width;
}

.page .content-area.no-sidebar {
  width: 100%;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  color: #fff;
  background-color: #333;
  padding: 15px;
  transition: .3s;
}

.page .sidebar.no-sidebar {
  left: -250px;
}

.header .toggle-sidebar {
  cursor: pointer;
}
